/*
@import "fonts";
@import "reset";
@import "example";
*/
.bubble {
  color: #ffffff;
  font-family: "ARS", Arial, sans-serif;
  position: relative;
  display: inline-block;
  overflow: hidden;
  font-size: 22px;
  line-height: 26px;
  transition: all 0.5s;
  border-radius: 25%;
}

.bubble--rounded {
  border-radius: 25%/50%;
}

.bubble--elevated {
  box-shadow: 7px 7px 20px 0 rgba(0, 0, 0, 0.1);
}

.bubble--color-orange {
  color: #fd5300;
}

.bubble--picto {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: center;
  justify-content: center;
  min-height: 275px;
  border-top-left-radius: 0 !important;
}

.bubble--picto svg {
  display: block;
  width: 90px;
  height: auto;
  margin: 0 auto 25px;
}

.bubble--bg-orange {
  background-color: #fd5300;
  border-top-left-radius: 0 !important;
}

.bubble--bg-orange.bubble--transparent::after {
  background-color: #fd5300;
}

.bubble--bg-white {
  background-color: #ffffff;
  color: #fd5300;
}

.bubble--bg-white.bubble--transparent::after {
  background-color: #ffffff;
}

.bubble--bg-blue {
  background-color: #009db8;
  border-top-right-radius: 0 !important;
}

.bubble--bg-blue.bubble--transparent::after {
  background-color: #009db8;
}

.bubble--transparent {
  background-color: transparent;
}

.bubble--transparent::after {
  content: "";
  z-index: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.9;
}

@supports (mix-blend-mode: multiply) {
  .bubble--transparent::before {
    content: "";
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.75);
  }
  .bubble--transparent::after {
    opacity: 1;
    mix-blend-mode: multiply;
  }
}

.bubble--border-orange {
  border: 3px solid #fd5300;
}

.bubble--border-white {
  border: 3px solid #ffffff;
}

.bubble--corner-left {
  border-top-left-radius: 0 !important;
}

.bubble--corner-right {
  border-top-right-radius: 0 !important;
}

.bubble--bbloc .bubble__container, .bubble--bbloc-orange .bubble__container {
  padding-top: calc(28px/2 + 28px + 28px/2);
  padding-left: calc(40px/2 + 28px/2);
}

